$color-purple: #7e3af2;
$color-dark-blue: #0d0c2b;
$color-white: #ffffff;
$color-light-text: #e0e0f0;

.jkit-upgrade-popup-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.jkit-upgrade-popup {
  position: relative;
  width: 460px;
  height: 370px;
  border-radius: 10px;

  .upgrade-popup-close {
    position: absolute;
    top: 12px;
    right: 16px;
    background: transparent;
    border: none;
    color: $color-white;
    font-size: 24px;
    cursor: pointer;
    z-index: 99;
  }

  .upgrade-pro-wrapper {
    .upgrade-pro-content {
      .banner.center {
        width: 273px;
      }
    }
  }


}

.upgrade-pro-wrapper {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 10px;

  &.dashboard {
    border-radius: 0;

    .upgrade-pro-content {
      border-radius: 0;

      img {
        border: none !important;
        border-radius: 8px !important;
      }

      .content-title {
        font-weight: 700;
        font-size: 28px;
        line-height: 1.2;
        text-align: center;
        width: 253px;

      }
    }
  }

  .upgrade-pro-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    box-sizing: border-box;
    align-items: center;
    height: 100%;
    border-radius: 10px;

    .banner {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      height: inherit;

      &.pattern {
        border-radius: 10px;
        padding: 4px;
      }

      &.background {
        border: 4px solid;
        border-color: linear-gradient(180deg, rgba(255, 255, 255, 0.24) 0%, rgba(255, 255, 255, 0) 100%);
        border-radius: 16px;
        box-shadow: 0px 8px 32px 0px #00000033;
        border-radius: 10px;
      }

      &.center {
        width: 245px;
        height: auto;
        position: relative;
      }

      &.arrow {
        width: 53px;
        height: 49px;
        opacity: 1;
        top: 245px;
        left: 401px;
        transform: rotate(351deg);
      }

    }

    .upgrade-pro-button {
      z-index: 10;
      position: relative;
      margin: 20px auto 0;
      cursor: pointer;
      transition: all 0.5s ease;
      background: radial-gradient(112.43% 113.24% at 49.77% 0%, #BA70FF 3.52%, #2F47FF 100%);
      border-radius: 10px;
      overflow: hidden;

      &::before {
        content: "";
        position: absolute;
        inset: 0;
        padding: 2px;
        background: linear-gradient(180deg, #9760FF 0%, rgba(255, 255, 255, 0.1) 100%);
        box-shadow: 0px 4px 4px -4px #00000033;
        border-radius: 10px;
        mask:
          linear-gradient(#000 0 0) content-box,
          linear-gradient(#000 0 0);
        mask-composite: exclude;
      }


      .button-content-wrapper {
        color: white;
        display: flex;
        padding: 14px 20px;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        font-weight: 600;
        gap: 6px;
        border-radius: 10px;
        font-family: 'Sora';

      }

      svg,
      span {
        transition: all 0.5s ease;
      }
    }

    .upgrade-pro-button:hover {
      transform: scale(0.94);

      svg,
      span {
        opacity: 0.75;
      }
    }

    .content-title {
      z-index: 1;
      position: relative;
      font-stretch: normal;
      line-height: 1.2;
      text-align: center;
      color: #fff;
      font-family: "Sora", sans-serif;
      font-weight: 700;
      font-size: 22px;
      max-width: 368px;
      margin-top: 11px;

      .gradient-text {
        background: linear-gradient(92.79deg, #D15DF7 31.66%, #3B98FF 60.84%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        /* For non-webkit browsers */
        text-fill-color: transparent;
        /* For non-webkit browsers */
        position: relative;
      }
    }
  }
}