@use "./dashboard/snackbar.scss";
@use "./dashboard/dashboard.scss";
@use "./dashboard/elements.scss";
@use "./dashboard/element-icons.scss";
@use "./dashboard/theme-builder.scss";
@use "./dashboard/pro-banner.scss";


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

$primary_color : #9C4DFF;

#wpcontent {
    .snackbar {
        left: calc(160px + 20px);
        z-index: 1;
    }
}

#jkit-admin-notice {
    .f-install-notice {
        margin-right: 20px;
        margin-top: 50px;
    }
}

.folded {
    #wpcontent {
        .snackbar {
            left: calc(160px + 20px);
        }
    }
}

@keyframes press {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.5);
    }

    to {
        transform: scale(1);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes blinking {
    0% {
        opacity: 0;
    }

    10% {
        opacity: 1;
    }

    20% {
        opacity: 0;
    }

    30% {
        opacity: 1;
    }

    40% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    60% {
        opacity: 0;
    }

    70% {
        opacity: 1;
    }

    80% {
        opacity: 0;
    }

    90% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.f-install-notice,
.jeg-elementor-kit-dashboard,
.essential-importer-modal {
    .loading {
        margin: 0 auto;
        display: block;
        text-align: center;

        svg {
            animation: spin 2s infinite linear;
        }

        &.disabled {
            background-color: #99a2a9;
        }

        &.expire {
            background-color: #e4b200;
            color: #ffffff;
        }
    }
}

.jeg-elementor-kit-dashboard {
    padding: 0 20px;
    margin: 0 auto;
    min-width: 1213px;
    max-width: 1213px;
    background: #f4f5f7;

    .f-search-wrapper {
        margin: 30px;
        display: flex;
        align-items: center;
        position: relative;

        input[type="text"] {
            width: 190px;
            height: 40px;
            background: #fafafa;
            border: 1px solid #d0d5dd;
            border-radius: 5px;
            padding: 0 12px;

            &::placeholder {
                font-family: "Inter", sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 17px;
                color: #7d8292;
            }
        }

        svg {
            position: absolute;
            right: 0;
            margin-right: 10px;
        }
    }

    // Navigation
    .f-navigation {
        .f-navigation-wrapper {
            box-sizing: border-box;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            margin-top: 35px;
            height: 70px;
            border-radius: 5px;
            background: #ffffff;
            box-shadow: 0px 10px 60px rgba(191, 197, 208, 0.2);
            z-index: 99;
            position: relative;
            padding: 15px 30px;

            .f-logo-wrapper {
                display: flex;
                align-items: center;
                gap: 8px;

                .f-navigation-logo {
                    img {
                        width: auto;
                        height: 32px;
                    }
                }
            }

            .f-navigation-wrapper-items {
                .navigation__button {
                    display: flex;
                    align-items: center;

                    .upgrade-pro-btn {
                        background: linear-gradient(180deg, #FFB12D 0%, #F59E0B 100%);
                        font-family: "Inter", sans-serif;
                        padding: 12px 18px;
                        border-radius: 6px;
                        text-decoration: none;
                        color: #fff;
                        display: flex;
                        gap: 10px;
                        align-items: center;
                        transition: all 0.3s ease;

                        &:hover {
                            transform: scale(0.94);
                            background: linear-gradient(180deg, #F1A627 0%, #E89812 100%);
                        }
                    }
                }

                .f-navigation__items {
                    display: flex;
                    gap: 30px;
                    justify-content: flex-end;

                    .f-navigation__item {
                        padding: 26px 5px;
                        font-family: "Inter", sans-serif;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 17px;
                        color: #7d8292;
                        text-decoration: none;
                        position: relative;

                        .f-navigation__subitem {
                            padding: 10px 20px;
                        }

                        .f-submenu__item {
                            position: absolute;
                            top: 55px;
                            margin: 0 auto;
                            left: -16px;
                            right: 0;
                            background: #fff;
                            border-bottom-left-radius: 5px;
                            border-bottom-right-radius: 5px;
                            box-shadow: 0 10px 60px rgba(191, 197, 208, 0.2);
                            width: 144px;
                            opacity: 0;
                            pointer-events: none;
                            transition: opacity 0.3s ease;
                            display: grid;
                            padding: 10px 10px 10px 0;

                            a {
                                position: relative;
                                height: unset;

                                svg {
                                    position: absolute;
                                    left: -180px;
                                    top: 2px;
                                    opacity: 0;
                                    transition: opacity 0.3s ease;
                                }

                                &:hover {
                                    svg {
                                        opacity: 1;
                                    }
                                }
                            }

                            .f-navigation__subitem svg {
                                margin: unset;
                                left: 0;
                            }
                        }

                        &:focus {
                            box-shadow: none;
                        }

                        svg {
                            position: absolute;
                            top: 0px;
                            margin: 0 auto;
                            left: 0;
                            right: 0;
                            opacity: 0;
                            transition: opacity 0.3s ease;
                        }

                        &:hover {
                            svg {
                                opacity: 1;
                            }

                            .f-submenu__item {
                                opacity: 1;
                                pointer-events: auto;
                            }
                        }

                        &.f-navigation__item--active {
                            color: #0f1538;

                            svg {
                                opacity: 1;

                                path {
                                    fill: #9747FF;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    //System Status
    .f-system {
        background: #fff;
        border-radius: 5px;
        margin-top: 30px;
        padding: 30px;
    }

    .f-system {
        padding: 0 0 60px;
        font-family: "Inter", sans-serif;

        .system-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            min-height: 90px;
            margin: 0 30px;
            border-bottom: 1px solid #d0d5dd;
            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
        }

        .system-body {
            padding: 40px 30px 0px;

            table {
                width: 100%;
                border: 1px solid #d0d5dd;
                border-radius: 5px;
                overflow: hidden;

                thead {
                    th {
                        text-align: left;
                        line-height: 1.5em;
                        padding: 12px 10px;
                        border-bottom: 1px solid #d0d5dd;
                        font-family: "Inter", sans-serif;
                        font-style: normal;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 19px;
                    }
                }

                tbody {
                    tr:nth-child(2n) {
                        background: #f8f9fb;
                    }

                    td {
                        font-size: 14px;
                        line-height: 1.4em;
                        padding: 10px;
                        vertical-align: middle;

                        &.status-title {
                            width: 35%;
                        }

                        &.status-flag {
                            width: 12px;

                            .status-flag-circle {
                                border-radius: 100%;
                                display: inline-block;
                                height: 12px;
                                margin: 0 auto;
                                vertical-align: middle;
                                width: 12px;

                                &.yellow {
                                    background: #eca61a;
                                }

                                &.green {
                                    background: #3a9551;
                                }

                                &.red {
                                    background: #e73030;
                                }
                            }
                        }

                        &.status-value {
                            .status-wrapper {
                                height: 100%;

                                span {
                                    font-size: 12px;
                                    font-style: italic;
                                }

                                svg {
                                    position: relative;
                                    top: 2px;
                                }

                                mark {
                                    &.yes {
                                        color: #3a9551;
                                        font-weight: 700;
                                        background: transparent;
                                    }
                                }
                            }
                        }
                    }
                }
            }

        }

        .header-button {
            background: #9747FF;
            border-radius: 6px;
            display: flex;
            text-decoration: none;
            cursor: pointer;
            width: 158px;
            padding: 10px 16px 10px 14px;

            &:hover {
                background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
            }

            &.load {
                margin: unset;
                background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);

                &.copied {
                    margin: unset;
                    background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
                }
            }

            &.copied {
                margin: unset;
                background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
            }

            .copy-button {
                text-align: center;
                font-family: "Inter", sans-serif;
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                display: flex;
                align-items: center;
                color: #ffffff;
                width: 100%;

                span {
                    align-items: center;
                    margin: 0 auto;
                    display: flex;
                }

                .loading {
                    height: 17px;
                    margin: 0 auto;
                    width: 100%;

                    svg {
                        margin: 0 auto;
                    }
                }

                svg {
                    margin-right: 5px;
                }
            }
        }

        .tooltip-wrapper {
            position: relative;
            display: flex;
            justify-content: center;
        }
    }

    .f-demo-wrapper {
        margin-top: 40px;

        .f-demo-heading {
            h2 {
                font-family: "Inter", sans-serif;
                font-style: normal;
                font-weight: 600;
                font-size: 28px;
                line-height: 45px;
                color: #0f1538;
                margin: 0 auto;
                width: fit-content;
            }

            span {
                margin: 10px auto 0 auto;
                width: 540px;
                color: #7d8292;
                text-align: center;
                font-family: "Inter", sans-serif;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 23px;
                text-align: center;
                display: block;
            }
        }

        .f-demo-heading-button-wrapper {
