                padding: 11px 15px;
                margin: 0 auto;
                font-family: "Inter", sans-serif;
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                display: flex;
                align-items: center;
                color: #0f1538;
                transition: all 0.3s ease;

                &:not(.tab-imported) {
