                &.tab-imported {
                    svg {
                        display: block;
                        margin-right: 7px;

                        path {
                            stroke: #0f1538;
                            transition: stroke 0.3s ease;
                        }
                    }
                }

                &.active {
                    background: #9747FF;
                    color: white;
                    border: 1px solid var(--border-color, #9747FF);

                    &:hover {
                        background: #9747FF;
                        color: white;
                        border: 1px solid var(--border-color, #9747FF);

                        &:not(.tab-imported) {
                            svg {
                                display: block;
                                margin-right: 7px;

                                path {
                                    fill: white;
                                }
                            }
                        }

                        &.tab-imported {
                            svg {
                                display: block;
                                margin-right: 7px;

                                path {
                                    stroke: white;
                                }
                            }
                        }
                    }

                    &:not(.tab-imported) {
                        svg {
                            display: block;
                            margin-right: 7px;

                            path {
                                fill: white;
                            }
                        }
                    }

                    &.tab-imported {
                        svg {
                            display: block;
                            margin-right: 7px;

                            path {
                                stroke: white;
                            }
                        }
                    }
                }

                &:hover {
                    border: 1px solid rgba(235, 214, 255, 1);
                    background: rgba(247, 239, 255, 1);
                    color: #9747FF;

                    &:not(.tab-imported) {
                        svg {
                            display: block;
                            margin-right: 7px;

                            path {
                                fill: #9747FF;
                            }
                        }
                    }

                    &.tab-imported {
                        svg {
                            display: block;
                            margin-right: 7px;

                            path {
                                stroke: #9747FF;
                            }
                        }
                    }
                }
            }
        }


        .f-demo-demo-inner-body {
            display: flex;
            gap: 30px;
            margin-top: 50px;

            .f-filter-sidebar {
                width: 207px;
                background: rgba(255, 255, 255, 1);
                box-shadow: 0px 0px 20px 0px rgba(191, 197, 208, 0.3);
                padding: 30px;
                border-radius: 8px;
                padding-right: 14px;
                overflow-x: hidden;
                height: max-content;

                .f-filter-sidebar-lists {
                    margin-top: 16px;
                    height: 753px;
                    overflow-x: hidden;
                    overflow-y: auto;
                    display: flex;
                    flex-direction: column;
                    gap: 15px;
                    padding-right: 8px;

                    &::-webkit-scrollbar {
                        width: 0px;
                    }

                    &:hover {
                        padding-right: 0;

                        &::-webkit-scrollbar {
                            width: 8px;
                        }
                    }





                    &:hover::-webkit-scrollbar {
                        height: 100px;
                    }

                    &::-webkit-scrollbar-track {
                        background: transparent;
                        border-radius: 3px;
                    }

                    &::-webkit-scrollbar-thumb {
                        background: #D9D9D9;
                        border-radius: 8px;
                    }

                    .checkbox-container {
                        display: flex;
                        gap: 10px;
                        align-items: center;
                        cursor: pointer;

                        label {
                            font-family: "Inter", sans-serif;
                            font-weight: 500;
                            font-size: 14px;
                            line-height: 16px;
                            text-align: left;
                            text-transform: capitalize;
                            cursor: pointer;
                        }

                        input[type="checkbox"] {
                            position: absolute;
                            pointer-events: none;
                            opacity: 0;
                            width: 0;
                            height: 0;
                        }
                    }
                }

            }

            .f-demo-demo-inner {
                width: 100%;

                .demo-navigation-wrapper {
                    background: #fff;
                    display: flex;
                    box-shadow: 0px 10px 20px 0px #BFC5D04D;
                    padding: 20px;
                    border-radius: 8px;
                    margin-bottom: 30px;
                    gap: 30px;

                    .f-demo-search-field {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 100%;
                        position: relative;

                        input[type="text"] {
                            width: 100%;
                            height: 44px;
                            opacity: 1;
                            border-radius: 6px;
                            padding: 13.5px 13.5px 13.5px 45px;
                            font-family: "Inter", sans-serif;
                            font-weight: 400;
                            font-size: 14px;
                            border: 1px solid rgba(209, 219, 245, 1);
                            transition: border-color 0.3s ease;

                            &::placeholder {
                                font-family: "Inter", sans-serif;
                                font-weight: 400;
                                font-size: 14px;
                                color: rgba(130, 133, 157, 1);
                            }

                            &:focus {
                                border-color: $primary_color;
                                box-shadow: none;
                            }
