                            left: 17px;
                        }
                    }

                    .f-demo-navigation-bottom-wrapper {
                        display: flex;
                        gap: 4px;
                        background: #F9F9FB;
                        border-radius: 8px;
                        border: 1px solid #D1DBF5;
                        align-items: center;
                        font-family: 'Inter';
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 20px;
                        padding: 4px;

                        .f-heading-button {
                            display: flex;
                            align-items: center;
                            padding: 8px 12px;
                            gap: 6px;
                            white-space: nowrap;
                            cursor: pointer;
                            border-radius: 4px;
                            transition: background .3 ease;
                            font-family: 'Inter';
                            font-weight: 600;
                            font-size: 14px;
                            line-height: 20px;
                            color: #717680;

                            &.active {
                                background: #FFFFFF;
                                box-shadow: 0px 1px 3px 0px #0A0D121A;
                                color: #414651;

                                &.tab-favorite {
