                                    svg {
                                        path {
                                            stroke: #414651;
                                        }
                                    }
                                }

                            }

                            &.tab-favorite {
                                &:hover {
                                    svg {

                                        path {
                                            fill: $primary_color;
                                        }
                                    }
                                }
                            }

                            &.tab-imported {
                                &:hover {

                                    svg {
                                        path {
                                            stroke: $primary_color;
                                        }
                                    }
                                }
                            }

                            &:hover {
                                color: $primary_color;


                            }
                        }
                    }

                }
            }
        }

        .demo-items-wrapper {
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
            grid-gap: 30px;

            .demo-like-wrapper {
                width: 23px;
                height: 23px;
                background: #f4f5f7;
                border-radius: 97.8208px;
                display: inline-flex;
                align-items: center;
                cursor: pointer;
                position: relative;
                overflow: hidden;
                transition: background 0.3s ease;

                &.active {
                    background: #ffeaf1;
                }

                .demo-like {
                    cursor: pointer;
                    margin: 0 auto;

                    svg {
                        fill: #fff;
                        filter: drop-shadow(0px 2px 5px rgba(52, 69, 101, 0.08));
                        display: block;
                        transition: fill 0.3s ease;
                    }

                    &.active {
                        svg {
                            path {
                                fill: #ff005c;
                            }
                        }
                    }
                }

                &.animate {
                    &.animate {
                        svg {
                            animation: press 0.2s 1 linear;
                        }
                    }
                }
            }

            .demo-item {
                background: #ffffff;
                position: relative;
                transition: box-shadow 0.3s ease;
                width: fit-content;
                height: fit-content;
                border-radius: 8px;
                padding: 22px;
                box-shadow: 0px 10px 20px 0px rgba(191, 197, 208, 0.3);

                &.favorite {
                    opacity: 0;
                    transition:
                        opacity 0.3s ease,
                        box-shadow 0.3s ease;

                    &.visible {
                        opacity: 1;
                    }
                }

                &:hover {
                    box-shadow: 0px 9.78208px 19.5642px rgba(191, 197, 208, 0.3);
                }

                .demo-item-loader {
                    display: flex;
                    position: absolute;
                    top: 50%;
                    right: 50%;
                    transform: translate(50%, -50%);

                    >div {
                        display: flex;
                    }
                }

                .demo-item-holder {
                    border-radius: 4px;
                    width: 260px;
                    overflow: hidden;
                    height: 260px;
                    box-shadow: 0px 1px 7px 2px #0F15380D;

                    img {
                        border-radius: 4px;
                        width: 100%;
                        display: block;
                        height: 260px;
                        object-fit: cover;
                        object-position: top center;
                    }
                }

                .demo-item-detail {
                    display: flex;
                    font-family: "Heebo";
                    font-size: 12px;
                    align-items: center;
                    margin-bottom: 11px;
                    justify-content: space-between;

                    span {
                        color: var(--primary, #0f1538);
                        font-family: "Inter", sans-serif;
                        font-size: 18px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: normal;
                    }

                    .demo-title-wrapper {
                        display: flex;
                        align-items: center;

                        .demo-new-flag {
                            align-items: center;
                            display: flex;
                            padding: 0px 8px 0px 8px;
                            height: 19px;
                            margin-left: 7px;
                            color: var(--white, #fff);
                            font-family: "Inter", sans-serif;
                            font-size: 9px;
                            font-style: normal;
                            font-weight: 500;
                            line-height: normal;
                            border-radius: 15px;
                            background: linear-gradient(180deg, #e7b73f 0%, #e7693f 100%);
                            box-shadow: -1px -1px 2px 0px rgba(19, 11, 30, 0.2) inset;
                        }
                    }
                }

                .demo-item-button {
                    margin-top: 20px;
                    display: flex;
                    gap: 13px;

                    .f-demo-import-button {
                        color: #ffffff;
                        width: 100%;
                        height: 35px;
                        background: linear-gradient(181.06deg, #9747FF 0.91%, #9747FF 124.87%);
                        border: 1px solid #9747FF;
                        border-radius: 6px;
                        cursor: pointer;
                        align-items: center;
                        display: flex;
                        transition: all 0.3s ease;

                        span {
                            color: var(--white, #fff);
                            text-align: center;
                            font-family: "Inter", sans-serif;
                            font-size: 11.738px;
                            font-style: normal;
                            font-weight: 500;
                            line-height: normal;
                            display: flex;
                            text-align: center;
                            width: fit-content;
                            margin: 0 auto;
                        }

                        &:hover {
                            background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
                        }
                    }

                    .f-button-activate-license {
                        text-decoration: none;
                    }

                    .f-button-upgrade {
                        background: linear-gradient(180deg, #FFB12D 0%, #F59E0B 100%);
                        border: 1px solid #F59E0B;
                        display: flex;
                        gap: 6px;
                        justify-content: center;
                        transition: all 0.3s ease;

                        &:hover {
                            background: linear-gradient(180deg, #F1A627 0%, #E89812 100%);
                            transform: scale(0.94);
                            color: #FFFFFFBF;
                        }

                        span {
                            margin: unset;
                        }
                    }

                    .f-demo-preview-button {
                        width: 100%;
                        height: 35px;
                        background: rgba(247, 239, 255, 1);
                        border: 1px solid rgba(235, 214, 255, 1);
                        border-radius: 6px;
                        cursor: pointer;
                        align-items: center;
                        display: flex;
                        color: #9747FF;
                        transition: all 0.3s ease;

