                background: #d31c3e;
                border: 1px solid #d31c3e;
                color: var(--white, #fff);
            }
        }

        .essential-uninstall-second-button {
            width: 100%;
            cursor: pointer;
            background: #ffeef1;
            border-radius: 4px;
            padding: 12px;
            text-align: center;
            border: 1px solid #ffc0cc;

            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            color: #f22047;
            transition:
                background 0.3s ease,
                color 0.3s ease,
                border 0.3s ease;

            &:hover {
                background: #f22047;
                border: 1px solid #f22047;
                color: var(--white, #fff);
            }
        }

        .essential-cancel-button {
            width: 100%;
            cursor: pointer;
            background: #fff;
            border-radius: 4px;
            padding: 12px;
            text-align: center;
            border: 1px solid #ffc0cc;

            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            color: #f22047;

            transition:
                background 0.3s ease,
                color 0.3s ease,
                border 0.3s ease;

            &:hover {
                background: #ffeef1;
                border: 1px solid #ffc0cc;
            }
        }

        .essential-footer-import {
            display: flex;
            position: relative;

            span {
                color: var(--text-color, #515676);
                font-family: "Inter", sans-serif;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: normal;
                margin-bottom: 15px;

                &:last-child {
                    right: 0;
                    position: absolute;
                }
            }
        }

        .essential-footer-import-progress {
            display: flex;
            position: relative;
            height: 15px;

            span {
                display: flex;
                height: 15px;
                border-radius: 100px;
                position: absolute;
                transition: width 0.3s ease;
            }

            span:first-child {
                background: rgba(103, 62, 230, 0.08);
                width: 100%;
            }

            span:last-child {
                background: linear-gradient(224.65deg, #A340FF 0%, #7F23FF 100%);
            }
        }
    }
}

.f-global-color-picker {
