/* Compontent */

.f-theme-content-save {
    cursor: pointer;
    display: flex;
    align-items: center;
    background: #e4e4e4;
    transition:
        background 0.3s ease,
        border 0.3s ease;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 5px;
    cursor: not-allowed;

    svg {
        path {
            stroke: #b3b3b3;
        }
    }

    &.save {
        background: linear-gradient(180deg, #A05DF7 0%, #6839A5 166.25%);
        cursor: pointer;

        span {
            color: #fff;
        }

        svg {
            path {
                stroke: #fff;
            }
        }

    }

    &.load {
        background: #F5F5F5;
        border: 1px solid #E9EAEB;

        span {
            color: #A4A7AE;
            font-family: "Inter";
            font-weight: 600;
            font-size: 12px;
            line-height: 18px;
        }

        svg {
            path {
                stroke: #A4A7AE;
            }
        }

        &.saved {
            background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
        }
    }

    &.saved {
        background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
    }

    span {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        color: #B3B3B3;
        align-self: center;
        display: flex;
        margin: 0 auto;
        align-items: center;

        svg {
            margin-right: 8px;
        }
    }

    >svg {
        padding-left: 15px;
    }

    .loading {
        display: flex;
        gap: 10px;
        flex-direction: row;
        align-content: center;
        align-items: center;

        svg {
            color: #a4a7ae;
        }
    }
}