$primary_color : #9C4DFF;
$boder_color : #E5E5E8;
$danger_color : #F04343;

h3,
p,
li {
    padding: 0;
    margin: 0;
    font-family: 'Inter';

}

.f-content__theme-builder {
    .f-theme-sidebar-wrapper {
        padding-top: 30px;

        .f-theme-menu {
            font-weight: 600;
            font-size: 11px;
            line-height: 100%;
            letter-spacing: 2px;
            text-transform: uppercase;
        }

        .f-submenu {
            min-width: 225px;
        }
    }

    .f-theme-content-wrapper {

        .control-wrapper {
            display: flex;
            gap: 28px;

            .jkit-control {
                width: 100%;
            }

            .jkit-select-text {
                input {
                    width: 100%;
                }
            }
        }

        .f-theme-content-save {
            padding: 12px;
            width: 149px;
        }

        .f-theme-content {
            padding: 30px;

            .general {
                >.option-desc {
                    color: #83858D;
                }

                .control-wrapper {
                    margin-top: 20px;
                    margin-bottom: 12px;
                    align-items: center;

                    .jkit-control-wrapper {
                        width: 100%;
                    }

                    .jkit-text {
                        input {
                            border: 1px solid #D0D5DD;
                            padding: 10px 14px;
                            width: 100%;
                            border-radius: 8px;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 100%;
                            letter-spacing: 0%;

                            &::placeholder {
                                color: #ADAFB3
                            }

                        }

                    }
                }

                .option-desc {
                    a {
                        color: $primary_color;
                    }
                }
            }

            .template-lists {
                .template-header {
                    display: flex;
                    justify-content: space-between;
                    gap: 20px;

                    &.empty {
                        flex-direction: column;
                        gap: 28px;

                        .header-desc {
                            max-width: unset;
                        }
                    }

                    .header-action {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 20px;

                        .active-tabs {
                            display: flex;
                            gap: 10px;
                            align-items: center;

                            .tabs-button {
                                font-weight: 500;
                                font-size: 14px;
                                line-height: 24px;
                                color: #7D8194;
                                cursor: pointer;

                                span {
                                    margin-left: 4px;
                                }

                                &.active {
                                    color: $primary_color;

                                    span {
                                        color: #0F1538;
                                    }
                                }
                            }

                        }

                        .add-button {
                            padding: 10px 16px;
                            white-space: nowrap;
                            border: 1px solid #EBD6FF;
                            border-radius: 8px;
                            cursor: pointer;
                            background-color: #F7EFFF;
                            color: $primary_color;
                            transition: background-color 0.2s ease-in-out;

                            &:hover {
                                background-color: #EBD6FF;
                            }
                        }
                    }

                    .header-desc {
                        max-width: 470px;

                        h3 {
                            margin-bottom: 12px;
                            font-weight: 500;
                            font-style: Medium;
                            font-size: 16px;
                            letter-spacing: 0%;
                        }

                        p {
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            letter-spacing: 0%;
                            color: #83858D;
                        }
                    }
                }

                .list-heading {
                    display: grid;
                    grid-template-columns: 1fr auto auto auto auto auto auto;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 100%;
                    letter-spacing: 0%;
                    margin: 40px 25px 20px 20px;

                    li {
                        padding: 0;
                        width: 49px;
                        text-align: center;

                        &.status {
                            width: 170px;

                        }

                        &.delete {
                            width: 109px;
                        }

                        &.edit {
                            width: 106px;

                        }

                        &.setting {
                            width: 69px;

                        }

                        &.clone {
                            width: 66px;

                        }

                        &.priority {
                            width: 49px;

                        }




                        &:first-child {
                            width: auto;
                            text-align: left;
                        }
                    }
                }

                .list-content {
                    border: 1px dotted #CECED3;
                    padding: 20px;
                    border-radius: 8px;
                    max-height: 614px;
                    height: 614px;
                    overflow-y: scroll;


                    a {
                        display: inline-flex;
                        align-items: center;
                    }
                }

                .sortable-items {
                    display: grid;
                    grid-template-columns: 1fr auto auto auto auto auto auto;
                    background: #f5f5f6;
                    align-items: center;
                    border: 1px solid #E5E5E8;

                    li {
                        align-items: center;
                        display: inline-flex;
                        border-right: 1px solid #E5E5E8;

                        .item {
                            padding: 10px 34.5px;
                            box-sizing: border-box;
                            height: 48px;
                            display: flex;
                            align-items: center;
                        }

                        &.jkit-icon-button {
                            position: relative;

                            &:hover {
                                .tooltip-text {
                                    visibility: visible;
                                    opacity: 1;
                                }
                            }

                            .tooltip-text {
                                visibility: hidden;
                                background-color: #333;
                                color: #fff;
                                text-align: center;
                                padding: 5px 8px;
                                border-radius: 4px;
                                font-size: 12px;
                                white-space: nowrap;
                                position: absolute;
                                z-index: 1;
                                bottom: -34px;
                                left: 50%;
                                transform: translate(-50%, -50%);
                                opacity: 0;
                                transition: opacity 0.2s ease-in-out;
                                pointer-events: none;
                            }
                        }

                        &.tab-detail {
                            font-weight: 600;
                            font-size: 12px;
                            line-height: 100%;

                            svg {
                                margin-right: 12px;
                            }
                        }

                        &.jkit-select {
                            .item {
                                padding: 10px 12px;
                            }
                        }

                        .status-select-wrapper {
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 18px;
                            text-align: center;
                            position: relative;


                            .status-dropdown {
                                background: #fff;
                                border: 1px solid #E9EAEB;
                                padding: 4px;
                                border-radius: 6px;
                                position: absolute;
                                margin-top: 4px;
                                z-index: 99;
                                display: flex;
                                flex-direction: column;
                                gap: 1px;
                            }


                            .active-status {
                                position: relative;
                                font-weight: bold;
                                padding: 4px 8px;
                                text-transform: capitalize;
                                width: 100px;
                                display: flex;
                                justify-content: space-between;

                                &:hover {
                                    cursor: pointer;
                                }

                                &.handle {
                                    border: 1px solid #d0d5dd;
                                    background-color: #fff;
                                    border-radius: 6px;
                                    padding: 4px 8px;
                                    width: 108px;
                                }

                                &.options {
                                    font-weight: 500;
                                    width: 100px;
                                    border-radius: 6px;
                                    color: #4B506A;
                                    font-size: 12px;
                                    line-height: 18px;

                                    &:hover {
                                        background-color: #FAFAFA;
                                    }
                                }

                                .dots {
                                    height: 6.4px;
                                    width: 6.4px;
                                    border-radius: 50%;
                                    display: inline-block;
                                    background-color: green;
                                    margin-right: 6.8px;
                                }

                                &.inactive {
                                    .dots {
                                        background-color: red;
                                    }
                                }
                            }
                        }

                        svg {
                            width: 16px;
                            height: 16px;
                        }
                    }
                }
            }
        }
    }
}

.jkit-control-wrapper {
    input[type=text] {
        &:focus {
            box-shadow: none;
        }
    }
}

.jkit-dashboard-popup-overlay {
    font-family: 'inter';

    position: 'fixed';
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;

    .jkit-popup-content {
        background: #fff;
        border-radius: 8px;
        width: 500px;

        .jkit-popup-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 20px 30px;
        }

        .jkit-popup-body {
            border-bottom: 1px solid #E6E8EB;
            border-top: 1px solid #E6E8EB;
            display: flex;
            flex-direction: column;
            gap: 30px;
            padding: 30px;
            height: 550px;
            overflow: hidden;
            overflow-y: auto;

            .jkit-control {
                display: flex;
                flex-direction: column;
                gap: 8px;

                .control-label {
                    font-family: 'Inter';
                    font-weight: 500;
                    font-size: 14px;
                }

                .control-desc {
                    font-family: 'Inter';
                    font-weight: 400;
                    font-size: 12px;
                    color: #83858D;

                }

                input {
                    height: 40px;
                    width: 100%;

                }

                &.jkit-repeater {
                    .repeater-header {
                        display: flex;
                        justify-content: space-between;
                        border: 1px solid #e5e5e8;
                        background-color: #f5f5f6;
                        font-weight: 600;
                        font-size: 14px;
                        width: 100%;
                        box-sizing: border-box;
                        border-radius: 4px;

                        &.open {
                            border-radius: 4px 4px 0 0;

                            .header-label {

                                svg {
                                    rotate: 0deg;
                                }
                            }

                        }

                        svg {
                            margin-right: 8px;
                        }

                        .header-label {
                            padding: 14px 0 14px 14px;
                            cursor: pointer;
                            width: 100%;
                            display: flex;
                            align-items: center;

                            svg {
                                rotate: 270deg;
                                transition: all .3 ease;
                            }
                        }


                    }

                    .remove-butotn {
                        padding: 14px 14px 14px 0;
                        cursor: pointer;


                        svg {
                            &:hover {
                                path {
                                    stroke: $primary_color;

                                }
                            }
                        }

                    }
                }

                .repeater-body {
                    border: 1px solid #e5e5e8;
                    border-top: none;
                    border-radius: 0 0 8px 8px;
                    padding: 14px 15px 16px 17px;
                    margin-top: -8px;

                    .jkit-control-wrapper {
                        display: flex;
                        flex-direction: column;
                        gap: 8px;
                        margin-bottom: 8px;

                        .control-desc {
                            font-family: "Inter";
                            font-weight: 400;
                            font-size: 12px;
                            line-height: 1.2;
                            color: #83858D;
                        }
                    }
                }
            }
        }

        .add-condition-wrapper {
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            font-family: '';
            margin-top: 10px;

            h3 {
                font-weight: 600;
                font-size: 22px;

            }

            p {
                font-weight: 400;
                font-size: 14px;
                line-height: 18px;
                margin: 12px 0 0 0;
                max-width: 300px;
                color: #7D8292
            }


        }

        .add-condition-button {
            border: 1px solid #EBD6FF;
            border-radius: 8px;
            background-color: #F7EFFF;
            padding: 14px 16px;
            color: $primary_color;
            cursor: pointer;
            width: fit-content;
            margin: 0 auto;
            transition: background-color 0.2s ease-in-out;

            &:hover {
                background-color: #EBD6FF;
            }

            span {
                margin-right: 8.5px;
            }
        }
    }

    .jkit-popup-footer {
        display: flex;
        flex-direction: row-reverse;
        padding: 20px 30px;
    }
}

.jkit-control {
    &.jkit-text {
        input:focus {
            box-shadow: 0px 0px 0px 4px #F4EBFF, 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
            border: 1px solid $primary_color !important;
            outline: none;
        }

        &.jkit-error {
            input {
                border: 1px solid $danger_color;
            }

            .control-desc {
                color: $danger_color;
            }
        }
    }
}

.jkit-primary-button {
    background: linear-gradient(180deg, #AF6AF9 0%, #9C4DFF 100%);
    border: none;
    border-radius: 6px;
    color: #fff;
    padding: 12px 22px;
    cursor: pointer;
    transition: background .3 ease;

    &:hover {
        background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
    }
}

.jkit-icon-button {
    cursor: pointer;

    &.fill {
        &:hover {
            svg {
                path {
                    fill: $primary_color;
                }
            }

        }
    }

    &.stroke {
        &:hover {
            svg {
                path {
                    stroke: $primary_color;
                }
            }

        }
    }

}

.f-content__theme-builder .f-theme-content-wrapper .f-theme-content .template-lists.draft .list-heading li.edit {
    width: 64px;
}

.f-content__theme-builder .f-theme-content-wrapper .f-theme-content .template-lists.draft .list-heading li.status {
    width: 188px;
}